import React from "react";
import PrimaryLayout from '../layouts/PrimaryLayout';
import ListContainer from '../components/ListContainer/ListContainer';

// Static content
import Banner from '../../static/bannerSlider.png';

import { graphql } from 'gatsby';

export default ({ data }) => {
  return (
    <PrimaryLayout>
      <div className="container-fluid bannerStart">
        <div className="row">
          <div className="col-lg-12 text-center">
            <img src={Banner} alt="bannerSlider" className="py-5" />
          </div>
        </div>
      </div>
      <div className="container-fluid py-3">
        <div className="row">
          <div className="col-lg-6 acanPosts">
            <h6 className="pb-1">Latest Acan Releases</h6>
            <ListContainer postInfo={data} />
          </div>
          <div className="col-lg-6 nappyPosts">
            <h6 className="pb-1">Latest Nappy Releases</h6>
            <ListContainer postInfo={data} />
          </div>
        </div>
      </div>
    </PrimaryLayout>
  )
}


export const query = graphql`
{
  allWordpressPost {
    nodes {
      slug
      title
      excerpt
      categories {
        name
      }
    }
  }
}
`